body {
  font-family: "RobotoCondensed-Light", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  padding-inline-start: 0;
  margin-block-start: 0;
  padding-inline-end: 0; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.container {
  box-sizing: border-box;
  width: 100%;
  padding: 0 105px;
  position: relative; }

.site-header {
  color: #ffffff;
  background-image: url("../img/header-bg-gradient-2x.png");
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 50px; }

.site-header__top {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: space-between; }

.site-head__logo {
  margin: 0;
  padding: 0;
  width: 89px;
  height: 20px; }

.site-header__phone {
  font-family: "BebasNeue-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.03em;
  margin: 0;
  padding: 0; }

.site-header__left {
  width: 460px;
  margin: 0;
  padding: 120px 65px 0 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  position: relative; }

.site-header__city {
  margin: 0;
  padding: 0;
  font-family: "BebasNeue-Regular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 49px;
  line-height: 59px;
  letter-spacing: 0.03em;
  text-transform: uppercase; }

.site-header__header {
  margin: 0 0 80px 0;
  padding: 0;
  font-family: "BebasNeue-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 109px;
  line-height: 109px;
  text-align: right;
  letter-spacing: 0.03em;
  text-transform: uppercase; }

.site-header__services {
  font-size: 14px;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  left: 50px;
  top: 310px; }
  .site-header__services li {
    text-align: right; }

.site-header__purchase {
  width: 230px;
  height: 55px; }

.site-header__social {
  margin: auto 0 0 auto; }

.button {
  font-family: "RobotoCondensed-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent; }
  .button--white {
    color: #ffffff;
    border: 4px solid #ffffff; }

.social {
  font-size: 12px;
  line-height: 14px;
  list-style: none;
  padding: 0; }

.social__item {
  margin: 0;
  padding: 0 0 25px 0; }

.social__link {
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase; }
